.display {
  background-color: lightseagreen;
  min-height: 50vmin;
  border: 1vmin solid rgba(220, 20, 60, 0.5);
  box-shadow: 0 0 5px black;
  border-radius: 5%;
  font-size: calc(20px + 15vmin);
}

@media (min-width: 600px) {
  .display {
    max-width: 48vmin;
    min-height: 20vmin;
    margin: 0 auto;
    font-size: calc(20px + 5vmin);
  }
}
